.cards-container {
  --column-count: 1;
  padding: 50px 40px;

  display: grid;
  justify-content: center;
  grid-template-columns: repeat(var(--column-count), $card-width);
  row-gap: 46px;
  column-gap: 48px;

  @media (min-width: 488px) {
    --column-count: 2;
  }
  @media (min-width: 768px) {
    --column-count: 3;
  }
  @media (min-width: 1024px) {
    --column-count: 4;
  }
}
